import Vue from 'vue'
import Router from 'vue-router'
import nurseRecords from '@/components/babyskymng/nurs/list/nurseRecords'
import nurseMod from '@/components/babyskymng/nurs/list/nurseMod'
import babyskymng from '@/components/babyskymng/babyskymng'
import nurs from '@/components/babyskymng/nurs/nurs'
import list from '@/components/babyskymng/nurs/list/list'
import nurseArguments from '@/components/babyskymng/nurs/setting/nurseArguments'
import sisterArguments from '@/components/babyskymng/nurs/setting/sisterArguments'
//report
import satisfaction from '@/components/babyskymng/seriveReport/satisfaction'
import ServReqDisposeReport from '@/components/babyskymng/seriveReport/ServReqDisposeReport'
import CntrctIncome from '@/components/babyskymng/saleReport/CntrctIncome'
import OrderSource from '@/components/babyskymng/saleReport/OrderSource'
import RoomStateRevenue from '@/components/babyskymng/saleReport/RoomStateRevenue'
import SalesAction from '@/components/babyskymng/saleReport/SalesAction'
import SalesSummary from '@/components/babyskymng/saleReport/SalesSummary'
import tabdoctor from '@/components/babyskymng/nurReport/tabdoctor'
import tabnurse from '@/components/babyskymng/nurReport/tabnurse'
import tabsitter from '@/components/babyskymng/nurReport/tabsitter'
import ComplBillReport from '@/components/babyskymng/seriveReport/ComplBillReport'
import ServReqReport from '@/components/babyskymng/seriveReport/ServReqReport'

Vue.use(Router)

export default new Router({
  // mode:'history',
  // routes: [
  //   {
  //     path: '/babyskymng',
  //     name: '/babyskymng',
  //     component: babyskymng,
  //     children: [{ 
  //           path: '/nurs',
  //           name: '/nurs',
  //           component: nurs,
  //           children: [{ 
  //                   path: '/list',
  //                   component: list,
  //                   children: [{ 
  //                     path: '/nurseRecords',
  //                     component: nurseRecords,
  //                 },{
  //                   path: '/nurseMod',
  //                   component: nurseMod,
  //                 }]
  //         }]
  //   }]
  //   }
  // ]



  routes: [
    {
      path: '/nurseRecords',
      // path: '/',
      name: 'nurseRecords',
      component: nurseRecords,
    },{
      path: '/nurseMod/:usertype/:exterUserCode/:subsyCode',
      name: 'nurseMod',
      component: nurseMod,
    },{
      path: '/nurseArguments',
      name: 'nurseArguments',
      component: nurseArguments,
    },{
      path: '/sisterArguments',
      name: 'sisterArguments',
      component: sisterArguments,
    }
    ,{
      path: '/satisfaction',
      name: 'satisfaction',
      component: satisfaction,
    }
    ,{
      path: '/ComplBillReport',
      name: 'ComplBillReport',
      component: ComplBillReport,
    }
    ,{
      path: '/ServReqReport',
      name: 'ServReqReport',
      component: ServReqReport,
    }
    ,{
      path: '/ServReqDisposeReport',
      name: 'ServReqDisposeReport',
      component: ServReqDisposeReport,
    }
    ,{
      path: '/CntrctIncome',
      name: 'CntrctIncome',
      component: CntrctIncome,
    }
    ,{
      path: '/OrderSource',
      name: 'OrderSource',
      component: OrderSource,
    }
    ,{
      path: '/RoomStateRevenue',
      name: 'RoomStateRevenue',
      component: RoomStateRevenue,
    }
    ,{
      path: '/SalesAction',
      name: 'SalesAction',
      component: SalesAction,
    }
    ,{
      path: '/SalesSummary',
      name: 'SalesSummary',
      component: SalesSummary,
    }

    ,{
      path: '/tabdoctor',
      name: 'tabdoctor',
      component: tabdoctor,
    }
    ,{
      path: '/tabnurse',
      name: 'tabnurse',
      component: tabnurse,
    }
    ,{
      path: '/tabsitter',
      name: 'tabsitter',
      component: tabsitter,
    }
  ]
})


