

var echarts = require('echarts/lib/echarts')
require('echarts/lib/chart/pie')
require('echarts/lib/component/legend')
require('echarts/lib/component/title')

export default {
data() {
return {
all: '',
starttime: '选择开始时间',
value2: '结束时间',
startDate: "",
enddate: "",
demo2: '无效客户',
list1: [],
list4: [],
list5: [],
suboptions: [],
subvalue: "",
list6: {
text: '',
subtext: ''
},
subsy: {
token: '',
subsyCode: '',
queryDate: ''
},
endtime: '选择结束时间',
index: 0,
btn: 0
}
},

created() {
window.SubmitH5Date = this.getfacilityData;


},
methods: {
change(value) {
var start = this.starttime.split('-')[0] + this.starttime.split('-')[1] + this.starttime.split('-')[2]
var end = this.endtime.split('-')[0] + this.endtime.split('-')[1] + this.endtime.split('-')[2]
this.getCntrctIncomeStatis(start, end)
},
chartstart() {
let myChart = echarts.init(document.getElementById('chart'));
let option = {
backgroundColor: "#fff",
title: {
text: this.list6.text,
subtext: this.list6.subtext,
x: 'center',
y: '30%',
textStyle: {
fontWeight: 'normal',
fontSize: 12
},
subtextStyle: {
color: '#159695',
fontSize: 16
}
},
tooltip: {
trigger: 'item',
formatter: "{a} <br/>{b}: {c} ({d}%)"
},
legend: {
left: 'center',

bottom: "bottom",
orient: 'horizontal',
data: this.list5,
selectedMode: false,
},
series: [{
name: '访问来源',
type: 'pie',
radius: ['40%', '55%'],
center: ['50%', '40%'],
avoidLabelOverlap: true,
labelLine: {
normal: {
show: true
}
},
data: this.list4
}]
};
myChart.setOption(option);
var self = this
myChart.on('click', function (params) {
for (var i = 0; i < self.list4.length; i++) {
if (params.name == self.list4[i].name) {
option.title.subtext = '¥' + self.list4[i].value
option.title.text = self.list4[i].name + "累计收入"
}
}

myChart.setOption(option);

});
},
getsubsyCode() {
let self = this;
let params = {

"url": this.resource.SERVERTEST + "/babyskymng/nurs/getSubsyList",
"method": "get"

};
let successcallback = function (res) {

self.suboptions = []

if (res.getSubsyList.length != 0) {

for (var i = 0; i < res.getSubsyList.length; i++) {
self.suboptions.push({
value: res.getSubsyList[i].subsyCode,
label: res.getSubsyList[i].subsyName
})
}

self.subsy.subsyCode = self.suboptions[0].value;
self.change()



var timer1 = window.setTimeout(function () {
self.starttime = self.myTool.commonTool.getmonthFirst()
self.endtime = self.myTool.commonTool.getmonthEnd()
}, 100);
} else {

}
};
let failurecallback = function (res) {
self.suboptions = []


};
self.resource.resourceUnit(params, successcallback, failurecallback);
},
getCntrctIncomeStatis(start, end) {
this.$vux.loading.show({
text: '加载中'
})
let self = this;
let params = {
"url": this.resource.SERVERTEST + "/babyskymng/income/getCntrctIncomeStatis",

"method": "post",
"body": {




subsyCode: self.subsy.subsyCode,
queryStartDate: start,
queryEndDate: end
}
};
let successcallback = function (res) {
self.$vux.loading.hide()
if (res.data != null) {
self.all = res.data.total
self.list1 = res.data.cntrctIncomeList
self.list4 = [];
self.list5 = [];
for (var i = 0; i < res.data.cntrctIncomeList.length; i++) {
self.list4.push({
name: res.data.cntrctIncomeList[i].prodTypeName,
value: res.data.cntrctIncomeList[i].prodTypeTotal
})
self.list5.push(res.data.cntrctIncomeList[i].prodTypeName)
self.list6.text = res.data.cntrctIncomeList[0].prodTypeName + "累计收入"
self.list6.subtext = '¥' + res.data.cntrctIncomeList[0].prodTypeTotal
}
self.chartstart()
} else {

self.$vux.toast.text('暂无数据', 'bottom')
}
};
let failurecallback = function (res) {
self.$vux.loading.hide()
self.$vux.toast.text('暂无数据', 'bottom')

};
self.resource.resourceUnit(params, successcallback, failurecallback);
},
getfacilityData(fuc) {

this.subsy.token = fuc.token
this.subsy.subsyCode = fuc.subsyCode
}

},
mounted() {


var self = this;
self.starttime = self.myTool.commonTool.getmonthFirst();
self.endtime = self.myTool.commonTool.getmonthEnd();

this.getsubsyCode();
},
activated() {

},
deactivated() {

},
beforeDestroy() {}
}




