

var echarts = require('echarts/lib/echarts')
require('echarts/lib/chart/pie')
require('echarts/lib/component/legend')
require('echarts/lib/component/title')

export default {
data() {
return {

ehartsstatus: true,
sas: "12",
starttime: '1',
value2: '结束时间',
startDate: "",
enddate: "",
demo2: '无效客户',
list2title: '',
list: [{}],
list2: '',
suboptions: [],
subvalue: "",
list1: {
listname: [],
servReqTotalCount: {
servReqName: "",
genCateCode: ""
},
list: []
},
subsy: {
token: '',
subsyCode: '',
queryDate: '',
interUserCode: '',
},
endtime: '',
index: 0,
btn: 0
}
},



created() {
window.SubmitH5Date = this.getfacilityData;

},
methods: {
change(value) {

var start = this.starttime.split('-')[0] + this.starttime.split('-')[1] + this.starttime.split('-')[2]
var end = this.endtime.split('-')[0] + this.endtime.split('-')[1] + this.endtime.split('-')[2]
console.log(start)
console.log(end)
this.getrequestlist(start, end)

},
chartstart() {

let myChart = echarts.init(document.getElementById('chart'));
let option = {
backgroundColor: "#fff",
title: {
text: this.list1.servReqTotalCount.servReqName + '收到请求',
subtext: this.list1.servReqTotalCount.servReqCount + "次",
x: 'center',
y: '30%',
textStyle: {
fontWeight: 'normal',
fontSize: 12
},
subtextStyle: {
color: '#159695',
fontSize: 20
}
},
tooltip: {
trigger: 'item',
formatter: "{a} <br/>{b}: {c} ({d}%)"

},
legend: {
left: 'center',
bottom: "bottom",
orient: 'horizontal',
data: this.list1.listname,
selectedMode: false,
},
series: [{
name: '访问来源',
type: 'pie',
radius: ['40%', '60%'],
center: ['50%', '40%'],
avoidLabelOverlap: true,
labelLine: {
normal: {
show: true
}
},
data: this.list1.list
}]
};
myChart.setOption(option);
var self = this
myChart.on('click', function (params) {

for (var i = 0; i < self.list1.list.length; i++) {
if (params.name == self.list1.list[i].name) {

option.title.subtext = self.list1.list[i].value + "次"
option.title.text = self.list1.list[i].name + "收到请求"
let testtype = {
normal: {
position: 'outside',
formatter: function (e) {

if (e.percent == 0) {
return '0.00%'
} else {
return e.percent + '%'
}
},
textStyle: {
color: '',
fontSize: 12
}
}
}
self.list2 = [{
value: self.list[i].getServReqSatisListOutputBeanList.exellentTotal,
name: '非常满意',
selected: true,
label: testtype,
},
{
value: self.list[i].getServReqSatisListOutputBeanList.goodTotal,
name: '满意',
label: testtype,
},
{
value: self.list[i].getServReqSatisListOutputBeanList.normalTotal,
name: '一般',
label: testtype,
},
{
value: self.list[i].getServReqSatisListOutputBeanList.worseTotal,
name: '不满意',
label: testtype,
},
{
value: self.list[i].getServReqSatisListOutputBeanList.worstTotal,
name: '很不满意',

label: testtype,
},

]
if (self.list[i].getServReqSatisListOutputBeanList.totalCount == "0") {
self.list2title = '暂无该统计数据'
} else {
self.list2title = self.list[i].servReqName + '统计'
}
self.chart2start()
}

}
myChart.setOption(option);

});
},
chart2start() {
let myChart = echarts.init(document.getElementById('chart2'));
let option = {
title: {
text: this.list2title,

x: '40%',
y: '75%',
textStyle: {
fontWeight: 'normal',
fontSize: 12
},
subtextStyle: {
color: '#159695',
fontSize: 20
}
},
backgroundColor: "#fff",
tooltip: {
trigger: 'item',
formatter: "{a} <br/>{b} : {c} ({d}%)"
},
legend: {
left: 'center',
bottom: "bottom",
orient: 'horizontal',
data: ['非常满意', '满意', '一般', '不满意', '很不满意'],
selectedMode: false,
},
series: [{
name: '4',
type: 'pie',
radius: '45%',
selectedMode: 'single',
center: ['50%', '40%'],
data: this.list2,
avoidLabelOverlap: true,
itemStyle: {
emphasis: {
shadowBlur: 10,
shadowOffsetX: 0,
shadowColor: 'rgba(0, 0, 0, 0.5)'
}
}
}]
};
myChart.setOption(option);
},

next(index) {
this.btn = index;


},
prev() {
if (this.index === 0) {
this.index = this.list2.length - 1
} else {
--this.index
}
},
getsubsyCode() {
let self = this;
let params = {
"url": this.resource.SERVERTEST + "/babyskymng/nurs/getSubsyList",
"method": "get"
};


let successcallback = function (res) {
console.log("getsubsyCode============", res)
self.suboptions = []

if (res.getSubsyList.length != 0) {

for (var i = 0; i < res.getSubsyList.length; i++) {
self.suboptions.push({
value: res.getSubsyList[i].subsyCode,
label: res.getSubsyList[i].subsyName
})
}
console.log("self.subvalue============", self.suboptions)
self.subsy.subsyCode = self.suboptions[0].value;
self.change()




var timer1 = window.setTimeout(function () {
self.endtime = self.myTool.commonTool.getmonthEnd()
self.starttime = self.myTool.commonTool.getmonthFirst()

}, 100);
} else {

}
};
let failurecallback = function (res) {
self.suboptions = []

console.log("error------------------->" + JSON.stringify(res))
};
self.resource.resourceUnit(params, successcallback, failurecallback);
},
getrequestlist(start, end) {
this.$vux.loading.show({
text: '加载中'
})
let self = this;
let params = {

"url": this.resource.SERVERTEST + "/babyskymng/report/getServReqReportForWeb",

"method": "post",
"body": {
startDate: start,
endDate: end,

subsyCode: self.subsy.subsyCode
}
};
console.log(params)
let successcallback = function (res) {
self.$vux.loading.hide()
console.log(res)
if (res.data.servReqTotalCount == "0") {
self.$vux.toast.text('暂无数据', 'bottom')
self.ehartsstatus = false
console.log(self.ehartsstatus)
} else {
self.ehartsstatus = true
console.log(self.ehartsstatus)
self.list = res.data.getServReqReportOutputBeanList

self.list1.listname = [];
self.list1.list = []
let testtype2 = {
normal: {
position: 'outside',
formatter: '{b}{c}次',
textStyle: {
color: '',
fontSize: 12
}
}
}
for (var i = 0; i < res.data.getServReqReportOutputBeanList.length; i++) {
self.list1.listname.push(res.data.getServReqReportOutputBeanList[i].servReqName)
self.list1.list.push({
value: res.data.getServReqReportOutputBeanList[i].servReqCount,
name: res.data.getServReqReportOutputBeanList[i].servReqName,
label: testtype2
})
}
self.list1.servReqTotalCount.servReqName = res.data.getServReqReportOutputBeanList[0].servReqName
self.list1.servReqTotalCount.servReqCount = res.data.getServReqReportOutputBeanList[0].servReqCount
self.list2title = res.data.getServReqReportOutputBeanList[0].servReqName + '统计'
let testtype = {
normal: {
position: 'outside',
formatter: function (e) {
if (e.percent == 0) {
return '0.00%'
} else {
return e.percent + '%'
}
},
textStyle: {
color: '',
fontSize: 12
}
}
}
self.list2 = [{
value: res.data.getServReqReportOutputBeanList[0].getServReqSatisListOutputBeanList.exellentTotal,
name: '非常满意',
selected: true,
label: testtype,
},
{
value: res.data.getServReqReportOutputBeanList[0].getServReqSatisListOutputBeanList.goodTotal,
name: '满意',
label: testtype,
},
{
value: res.data.getServReqReportOutputBeanList[0].getServReqSatisListOutputBeanList.normalTotal,
name: '一般',
label: testtype,
},
{
value: res.data.getServReqReportOutputBeanList[0].getServReqSatisListOutputBeanList.worseTotal,
name: '不满意',
label: testtype,
},
{
value: res.data.getServReqReportOutputBeanList[0].getServReqSatisListOutputBeanList.worstTotal,
name: '很不满意',

label: testtype,
},

],


self.chartstart()
self.chart2start()

}
};
let failurecallback = function (res) {
self.$vux.loading.hide()
self.ehartsstatus = false
console.log(self.ehartsstatus)
self.$vux.toast.text('服务器繁忙', 'bottom')


};
self.resource.resourceUnit(params, successcallback, failurecallback);
},
},
mounted() {

var self = this;
self.starttime = self.myTool.commonTool.getmonthFirst();
self.endtime = self.myTool.commonTool.getmonthEnd();

this.getsubsyCode();
},
activated() {


},
deactivated() {

},
beforeDestroy() {}
}




