export default {

  name: 'nurseRecords',
  data() {
    return {
      dialogTitle: '',
      dialogVisible: false,
      formLabelWidth: '120px',
      form: {
        oldvalue: '',
        tranNcareDataRecordId: '',
        ncareBatchCode:"",
        newvalue: ''
      },
      intemStatus:'',
      input: "",
      value: "",
      value1: "",
      inputlist:[],
      checkboxlist: [],
      checkoption: [],
      checkvalue:"",
      unitName:'',
      parmBox: {
        type: '',
        exterUserCode: "",
        babyCode: "",
        subsyCode: "",
        time: ''
      },
      nurseBox: [],
      doctorBox: [],
      sitterBox: [],
      activeName2: 'first',
      pickerOptions1: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      nurselist: {
        list: [],
        msg: ""
      },
      doclist: {
        list: [],
        msg: ""
      },
      babycare: {
        list: [],
        msg: ""
      }
    }
  },
  filters: {　　　　
    myCurrency(val) {
      return new Date(parseInt(val) * 1).toLocaleString();　　　　
    }　　
  },
  methods: {
    opentip(index) {
      // console.log(index)
      this.boxshow = index + 1
    },
    getmasterData() {
      let self = this;
      let params = {
        // "url": this.HOST + "/babyskyrsb/common/getLatestAppMstData",
        "url": this.resource.SERVERTEST + "/babyskymng/common/getLatestAppMstData",
        "method": "post",
        "body": {
          // "subsyCode":"3130113001",
          "subsyCode":self.parmBox.subsyCode,
          "ncareUpdFlg":"1"
        }
      };
      let successcallback = function (res) {
        // console.log("res", res)
        self.inputlist= []
        self.checkboxlist =[]
        for(var i = 0 ; i < res.data.mstNcareItemBeanList.length ;i++){
          if(res.data.mstNcareItemBeanList[i].ncareItemTypeCode == "00170002"){
            self.inputlist.push({
              name:res.data.mstNcareItemBeanList[i].ncareItemName,
              unitName: res.data.mstNcareItemBeanList[i].unitName
            })
          }else if (res.data.mstNcareItemBeanList[i].ncareItemTypeCode == "00170004"){
            self.checkboxlist.push({
              code: res.data.mstNcareItemBeanList[i].ncareItemCode,
              name:res.data.mstNcareItemBeanList[i].ncareItemName,
              option: []
            })
          }
        }
        for(var i = 0 ; i < res.data.mstNcareItemOptionBeanList.length ;i++){
          for(var k = 0 ; k < self.checkboxlist.length ;k++){
            if(res.data.mstNcareItemOptionBeanList[i].ncareItemCode == self.checkboxlist[k].code){
            self.checkboxlist[k].option.push({
              value: res.data.mstNcareItemOptionBeanList[i].ncareItemOptionName,
              label: res.data.mstNcareItemOptionBeanList[i].ncareItemOptionName
            })
          }
          }
        }
        // console.log("self.inputlist===========",self.inputlist)
        // console.log("self.checkboxlist===========",self.checkboxlist)
        // if (res.data.ncareDataDtlBatchList != null) {

        // } else {
        //   // self.$vux.toast.text('暂无数据', 'bottom')
        // }
      
      };
      let failurecallback = function (res) {
        
        // self.$vux.toast.text('暂无数据', 'bottom')
        // console.log("error------------------->" + JSON.stringify(res))
      };
      self.resource.resourceUnit(params, successcallback, failurecallback);
    },
    showDialog(int,BatchCode) {
      this.checkoption = []
      this.unitName = ''
      this.form.newvalue = ''
      this.form.ncareBatchCode = BatchCode
      if(int.ncareItemTypeCode == "00170004"){
        this.intemStatus = "checkbox"
        for(var j = 0 ;j < this.checkboxlist.length; j++){
          if(this.checkboxlist[j].name == int.ncareItemName){
            this.checkoption = this.checkboxlist[j].option
          }
        }
        this.form.newvalue = this.checkoption[0].value
      }else{
        this.intemStatus = "input"
        for(var j = 0 ;j < this.inputlist.length; j++){
          if(this.inputlist[j].name == int.ncareItemName){
            this.unitName = this.inputlist[j].unitName
          }
        }
      }
      this.dialogVisible = true;
      this.form.tranNcareDataRecordId = int.tranNcareDataRecordId
      this.form.oldvalue = int.ncareItemVal;
      this.dialogTitle = int.ncareItemName
    },
    formSubmit() {
      let self = this;
      let params = {
        "url": this.resource.SERVERTEST + "/babyskymng/ncare/updNcareData",
        "method": "post",
        "body": {
          "tranNcareDataRecordId":self.form.tranNcareDataRecordId,
          "ncareItemVal": self.form.newvalue,
          "ncareBatchCode": self.form.ncareBatchCode,
        }
      };
      let successcallback = function (res) {
        // console.log("res", res)
        self.dialogVisible = false
        

        if (res.status == 200) {
          self.$notify.success({
            title: '成功',
            message: '已发出修改申请',
            showClose: false
          });

        } else {
          self.$notify.error({
            title: '错误',
            message: '修改申请提交失败',
            showClose: false
          });
          // self.$vux.toast.text('暂无数据', 'bottom')
        }
      
      };
      let failurecallback = function (res) {
        self.dialogVisible = false
        self.$notify.error({
            title: '错误',
            message: res.msg,
            showClose: false
          });
        // self.$vux.toast.text('暂无数据', 'bottom')
        // console.log("error------------------->" + JSON.stringify(res))
      };
      self.resource.resourceUnit(params, successcallback, failurecallback);
    },
    retSearch() {
      // console.log(this.value1)
      this.getnurseData(this.value1)
    },
    getnurseData(time) {
      let dataBox = [];
      if (this.parmBox.type == "mm") {
        dataBox = {
          "subsyCode": this.parmBox.subsyCode,
          "ncareTranCodeList": [
            "NBIZ1003", "NBIZ1001", "NBIZ1002"
          ],
          "requestSource": 1,
          "recordDate": this.parmBox.time,
          "exterUserCode": this.parmBox.exterUserCode
        }
      } else if (this.parmBox.type == "bb") {
        dataBox = {
          "subsyCode": this.parmBox.subsyCode,
          "ncareTranCodeList": [
            "NBIZ2003", "NBIZ2001", "NBIZ2002"
          ],
          "requestSource": 1,
          "recordDate": this.parmBox.time,
          "exterUserCode": this.parmBox.exterUserCode,
          "babyCode": this.parmBox.babyCode,
        }
      }
      let self = this;
      let params = {
        "url": this.resource.SERVERTEST + "/babyskymng/ncare/getNcareDataList",
        "method": "post",
        "body": dataBox
      };
      let successcallback = function (res) {
        // console.log("res", res)
        self.nurseBox = [];
        self.doctorBox = [];
        self.sitterBox = [];
        if (res.data.ncareDataDtlBatchList != null) {
          // console.log("res.data.ncareDataDtlBatchList", res.data.ncareDataDtlBatchList)
          for (var i = 0; i < res.data.ncareDataDtlBatchList.length; i++) {
            if (self.parmBox.type == "mm") {
              if (res.data.ncareDataDtlBatchList[i].ncareTranCode == "NBIZ1001") {
                self.nurseBox.push(res.data.ncareDataDtlBatchList[i])
              }
              if (res.data.ncareDataDtlBatchList[i].ncareTranCode == "NBIZ1002") {
                self.doctorBox.push(res.data.ncareDataDtlBatchList[i])
              }
              if (res.data.ncareDataDtlBatchList[i].ncareTranCode == "NBIZ1003") {
                self.sitterBox.push(res.data.ncareDataDtlBatchList[i])
              }
            } else if (self.parmBox.type == "bb") {
              if (res.data.ncareDataDtlBatchList[i].ncareTranCode == "NBIZ2001") {
                self.nurseBox.push(res.data.ncareDataDtlBatchList[i])
              }
              if (res.data.ncareDataDtlBatchList[i].ncareTranCode == "NBIZ2002") {
                self.doctorBox.push(res.data.ncareDataDtlBatchList[i])
              }
              if (res.data.ncareDataDtlBatchList[i].ncareTranCode == "NBIZ2003") {
                self.sitterBox.push(res.data.ncareDataDtlBatchList[i])
              }
            }
          }
        } else {
          // self.$vux.toast.text('暂无数据', 'bottom')
        }
        // console.log("nurseBox", self.nurseBox)
      };
      let failurecallback = function (res) {
        // self.$vux.toast.text('暂无数据', 'bottom')
        // console.log("error------------------->" + JSON.stringify(res))
      };
      self.resource.resourceUnit(params, successcallback, failurecallback);
    },
    retRecord() {
      this.$router.push("/nurseRecords")
    },
    getLocalTime(nS) {
      return new Date(parseInt(nS)).toLocaleString().replace(/:\d{1,2}$/, ' ');
    },
  },
  mounted() {
    this.parmBox.time = this.myTool.commonTool.gettodaydate()
    this.parmBox.type = this.$route.params.usertype
    this.parmBox.exterUserCode = this.$route.params.exterUserCode
    this.parmBox.babyCode = this.$route.params.babyCode
    this.parmBox.subsyCode = this.$route.params.subsyCode
    // console.log(this.parmBox)
    this.getnurseData()
    this.getmasterData()
    this.value1 = this.myTool.commonTool.getcurrdate()
    // console.log("time=========", this.getLocalTime(1515340580000))
  }
}




