import moment from 'moment'
class commonTool {
    //不为空
    isNotBlank(val) {
            if (typeof(val) == "undefined" || val == null || val == "") {
                return false;
            }
            return true;
        }
        //是空对象
    isEmptyObject(obj) {
        let test = JSON.stringify(obj)
        for (var key in JSON.parse(test)) {
            return false
        };
        return true
    }
    getcurrdate() {
        var myDate = new Date()
            // var days=myDate.getFullYear() + '-' + (myDate.getMonth() + 1) + "-" + myDate.getDate()
            // console.log( moment().add(2, 'days').format('YYYY-MM-DD'))
        return moment().format('YYYY-MM-DD')
    }
    getmonthdate(beginTime, month) {
        return moment(new Date(beginTime)).subtract(1, 'M').subtract(1, 'days').format('YYYY-MM-DD')
    }
    gettodaydate() {
        var myDate = new Date()
            // var days=myDate.getFullYear() + '-' + (myDate.getMonth() + 1) + "-" + myDate.getDate()
            // console.log( moment().add(2, 'days').format('YYYY-MM-DD'))
        return moment().format('YYYYMMDD')
    }
    gettimedate(value) {
        return moment(value).format('HHmmss')
    }
    checknumber(val) {
            if (!(/^([0-9]+)$/.test(val))) {
                return true;
            } else {
                return false;
            }
        }
        //正则是否手机号
    checkPhone(val) {
            if (!(/^1(3|4|5|7|8)\d{9}$/.test(val))) {
                return false;
            } else {
                return true;
            }
        }
        //app初始化清空
    clearInit(self) {
            // localStorage.removeItem("headers");
            // self.$auth.logout();
            self.$store.dispatch("initALL");
        }
        // 获取结束时间
    getEndTime(beginTime, month) {
        return moment(new Date(beginTime)).subtract(12, 'M').subtract(1, 'days').format('YYYY-MM-DD')
    }


    // 获取当前年月的第一天
    getmonthFirst() {
        return moment().startOf('month').format("YYYY-MM-DD")
    }

    // 获取当前年月的最后一天
    getmonthEnd() {
        return moment().endOf('month').format("YYYY-MM-DD")
    }

    rependTime(beginTime, month) {
        return moment(new Date(beginTime)).subtract(48, 'M').subtract(1, 'days').format('YYYY-MM-DD')
    }
    repstarTime() {
            var myDate = new Date()
            return moment().add(12, 'M').format('YYYY-MM-DD')
        }
        // 确认支付后返回页面的弹窗
    showConfirm(self, num, money) {
        self.$vux.confirm.show({
            // 组件除show外的属性
            title: '提示',
            content: '<p style="margin-bottom:10px">订单号：' + num + '</p><p style="margin-bottom:20px">订单金额：￥' + money + '</p>',
            confirmText: '支付成功',
            cancelText: '支付遇到问题',
            onConfirm() {
                self.$router.push("/home/homemodule")
            },
            // onCancel () {}
        })
    }
    androidmove() {
        var func = window.NativeInterface.getNativeInterUserInfo();
        return func
    }
    turnintoUnix() {

    }
    unixintoTime(time) {
        let changeTime = moment(time).unix()
        return moment(changeTime).unix()
    }
}

export default {
    commonTool: new commonTool()
}


