import Vue from 'vue'
import VueResource from 'vue-resource'
Vue.use(VueResource)

export default {
    SERVERROOT: 'http://192.168.1.190:8080',
    // SERVERTEST: 'https://app.babysky.com.cn:9442/babyskyrsb',
    SERVERTEST: '',
    // SERVERROOT: 'http://192.168.10.175:8288',
    version: "0.0.1",
    buildId: "1",
    wechart: "",
    codePushId: "1",
    app: Vue, //整个实例对象
    seturl(url) {
        // console.log(url)
        this.wechart = url
            // console.log(this.SERVERROOT)
    },
    resourceUnit(param, successcallback, errorcallback) {
        // console.log("--------param-------" + JSON.stringify(param.body))
        var self = this;
        let isTimeout = true;
        // Vue.http.headers.common['token'] = "9696f060c0323e496a53315dc246f329";
        // Vue.http.headers.common['token'] = "zhanglichao";c09e6422b9578a6e86b8eb2e072138ac
        // Vue.http.headers.common['token'] = "9696f060c0323e496a53315dc246f329";
        setTimeout(function() {
            if (isTimeout) {
                var data = {
                    code: 0,
                    msg: "连接超时，请稍后再试！"
                }
                if (errorcallback) {
                    errorcallback(data)
                } else {
                    // self.app.router.app.$children[0].toastTip(data.msg)
                }
            }
        }, (param.timeout + 1000) || 6000);
        Vue.http({
            // url: param.url + "?randomtime=" + (new Date()).getTime() + this.wechart,
            url: param.url ,
            method: param.method,
            body: param.body, //body参数，post请求使用此参数
            params: param.pa, //url参数
            headers: param.headers ,
            timeout: param.timeout == undefined ? 10000 : param.timeout
        }).then(
            function(response) {
                // console.log(response)
                isTimeout = false;
                // console.log(`params:------------${JSON.stringify(param.headers==undefined)}`)
                self.dealREsponse(response, successcallback, errorcallback);
            },
            function(response) {
                isTimeout = false;
                // console.log(`params:------------${JSON.stringify(param.headers)}`)
                self.dealREsponse(response, successcallback, errorcallback);
            })
            
    },

    dealREsponse(response, successcallback, errorcallback) {
        // alert(JSON.stringify(response))
        // console.log(response);
        var self = this;
        if (response.status == 200) {
            // if (response.body.type == "image/jpeg") {
            //     successcallback(response.data)
            //     return;
            // }
            // //http成功
            if (response.body.status == 200 || response.data.code == 200) {
                //成功
                if (successcallback) {
                    successcallback(response.data)
                } else {
                    // console.log('操作成功！')
                }
            } else {
                //我们的接口失败
                // console.log("o")
                if (errorcallback) {
                    errorcallback(response.data)
                } else {
                    // self.app.router.app.$children[0].toastTip(response.data.msg)
                }
            }
        } else if (response.status == 0) {
            //网络不通畅，连接超时
            var data = {
                code: 0,
                msg: "服务连接出错，请稍后再试！"
            }
            // if (errorcallback) {
            //     errorcallback(data)
            // } 
            // else {
            //     self.app.router.app.$children[0].toastTip(data.msg)
            // }
        } else if (response.status == 403) {
            // console.log(`params:------------${JSON.stringify(response.body.code)}`)
            // console.log(self.app)
            //强制下线
            // if (response.body.code && response.body.code == '403') {
            //     self.app.router.app.$children[0].forceOut()
            //     self.app.router.app.$router.push('/home/login')
            //         //  this.$router.push("/home/mine")
            // } else {
            //     //http失败
            //     var data = {
            //         code: 500,
            //         msg: "登陆已过期,请重新登陆!"
            //     }
            //     if (errorcallback) {
            //         errorcallback(data)
            //     } else {
            //         self.app.router.app.$children[0].toastTip(data.msg)
            //     }
            // }
        } else {
            //http失败
            var data = {
                code: 500,
                msg: "操作异常，请稍后再试！"
            }
            if (errorcallback) {
                errorcallback(data)
            } else {
                // self.app.router.app.$children[0].toastTip(data.msg)
            }
        }
    }
}


