

var echarts = require('echarts/lib/echarts')
require('echarts/lib/chart/bar')
require('echarts/lib/component/legend')
require('echarts/lib/component/title')

export default {
data() {
return {

ehartsstatus: true,
starttime: '选择开始时间',
value2: '结束时间',
demo2: '无效客户',
startDate: "",
enddate: "",
list2: ['无效客户', '未处理客户'],
endtime: '选择结束时间',
index: 0,
btn: 0,
suboptions: [],
subvalue: "",
list: {
list1: [],
list2: [],
list3: [],
subsyGetScore: '',
currentSubsyGetAvgScore: "",
sameSubsyGetAvgScore: ""
},
subsy: {
token: '',
subsyCode: '',
queryDate: '',
interUserCode: '',
},
}
},



created() {
window.SubmitH5Date = this.getfacilityData;

},
methods: {
change() {
var start = this.starttime.split('-')[0] + this.starttime.split('-')[1] + this.starttime.split('-')[2]
var end = this.endtime.split('-')[0] + this.endtime.split('-')[1] + this.endtime.split('-')[2]


this.getSatisSurveyReport(start, end)
},
chartstart() {

let myChart = echarts.init(document.getElementById('chart'));
let option = {














legend: {
left: 'center',
bottom: "bottom",
orient: 'horizontal',
data: ['会所各服务项目分值', '同品牌会所各服务项目分值'],
},
grid: {
bottom: '15%',
containLabel: true
},
xAxis: {
triggerEvent: true,
data: this.list.list1,

},
yAxis: {},
series: [{
name: '会所各服务项目分值',
type: 'bar',

itemStyle: {
normal: {
color: '#3ca588'
}
},
label: {
normal: {
show: true,
position: 'top'
}
},
data: this.list.list2
}, {
name: '同品牌会所各服务项目分值',
type: 'bar',
barGap: '0%',
itemStyle: {
normal: {
color: '#94cede'
}
},
label: {
normal: {
show: true,
position: 'top'
}
},
data: this.list.list3
}]
};
myChart.setOption(option);
},
getsubsyCode() {
let self = this;
let params = {

"url": this.resource.SERVERTEST + "/babyskymng/nurs/getSubsyList",
"method": "get"

};


let successcallback = function (res) {

self.suboptions = []

if (res.getSubsyList.length != 0) {

for (var i = 0; i < res.getSubsyList.length; i++) {
self.suboptions.push({
value: res.getSubsyList[i].subsyCode,
label: res.getSubsyList[i].subsyName
})
}

self.subsy.subsyCode = self.suboptions[0].value;
self.change()



var timer1 = window.setTimeout(function () {
self.starttime = self.myTool.commonTool.getmonthFirst()
self.endtime = self.myTool.commonTool.getmonthEnd()


}, 100);
} else {

}
};
let failurecallback = function (res) {
self.suboptions = []


};
self.resource.resourceUnit(params, successcallback, failurecallback);
},
getSatisSurveyReport(start, end) {
this.$vux.loading.show({
text: '加载中'
})
let self = this;
let params = {
"url": this.resource.SERVERTEST + "/babyskymng/report/getSatisSurveyReportForWeb",
"method": "post",






"body": {
startDate: start,
endDate: end,
subsyCode: self.subsy.subsyCode
}
};

let successcallback = function (res) {
self.$vux.loading.hide()

if (res.data.getSatisSurveyCateTypeOutputBean == null) {
self.$vux.toast.text('暂无数据', 'bottom')
self.ehartsstatus = false

} else {
if (res.data.subsyGetScore == "0.0") {

}
self.list.list1 = [];
self.list.list2 = [];
self.list.list3 = [];
for (var i = 0; i < res.data.getSatisSurveyCateTypeOutputBean.length; i++) {
self.list.list1.push(res.data.getSatisSurveyCateTypeOutputBean[i].satisCateTypeName)
self.list.list2.push(res.data.getSatisSurveyCateTypeOutputBean[i].currentSubsyScore)
self.list.list3.push(res.data.getSatisSurveyCateTypeOutputBean[i].sameSubsyScore)
}
self.list.subsyGetScore = res.data.subsyGetScore
self.list.currentSubsyGetAvgScore = res.data.currentSubsyGetAvgScore
self.list.sameSubsyGetAvgScore = res.data.sameSubsyGetAvgScore
self.chartstart()
}

};
let failurecallback = function (res) {
self.$vux.loading.hide()
self.ehartsstatus = false
self.$vux.toast.text('暂无数据', 'bottom')

};
self.resource.resourceUnit(params, successcallback, failurecallback);
},
},
mounted() {



var self = this;
self.starttime = self.myTool.commonTool.getmonthFirst();
self.endtime = self.myTool.commonTool.getmonthEnd();

this.getsubsyCode();
},
activated() {


},
deactivated() {

},
beforeDestroy() {}
}




