

import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
data() {
return {

seller: '',
sellerlist: [],
showScrollBox: false,
starttime: '选择开始时间',
value2: '结束时间',
demo2: '无效客户',
list2: ['无效客户', '未处理客户'],
endtime: '选择结束时间',
index: 0,
btn: 0,
startDate: "",
enddate: "",
suboptions: [],
subvalue: "",
subsy: {
token: '',
subsyCode: '',
queryDate: ''
},
dome: [{
'name': '8021',
value1: '25',
value2: '34',
value3: '42',
value4: 10,
}],
users: [],
title: ['销售员', '咨询客户', '参观客户', '签约客户', '参观率', '签约率', ]
}
},

created() {
window.SubmitH5Date = this.getfacilityData;


},
methods: {
change(value) {
this.getRoomStateRevenueReport(this.starttime, this.endtime)
},
showdialog(name, list) {
this.sellerlist = [];
this.sellerlist = list;

this.seller = name;
this.showScrollBox = true
},
getsubsyCode() {
let self = this;
let params = {

"url": this.resource.SERVERTEST + "/babyskymng/nurs/getSubsyList",
"method": "get"
};
let successcallback = function (res) {

self.suboptions = []

if (res.getSubsyList.length != 0) {

for (var i = 0; i < res.getSubsyList.length; i++) {
self.suboptions.push({
value: res.getSubsyList[i].subsyCode,
label: res.getSubsyList[i].subsyName
})
}

self.subsy.subsyCode = self.suboptions[0].value;
self.change()



var timer1 = window.setTimeout(function () {
self.starttime = self.myTool.commonTool.getmonthFirst()
self.endtime = self.myTool.commonTool.getmonthEnd()

}, 100);
} else {

}
};
let failurecallback = function (res) {
self.suboptions = []


};
self.resource.resourceUnit(params, successcallback, failurecallback);
},
getRoomStateRevenueReport(start, end) {
this.$vux.loading.show({
text: '加载中'
})
let self = this;
let params = {
"url": this.resource.SERVERTEST + "/babyskymng/sale/getRoomStateRevenueReport",


"method": "post",



"body": {




subsyCode: self.subsy.subsyCode,
startDate: start,
endDate: end



}
};

let successcallback = function (res) {
self.$vux.loading.hide()
if (res.data.length != 0) {
self.users = res.data

} else {
self.users = res.data
self.$vux.toast.text('暂无数据', 'bottom')
}
};
let failurecallback = function (res) {
self.$vux.loading.hide()
self.$vux.toast.text('暂无数据', 'bottom')

};
self.resource.resourceUnit(params, successcallback, failurecallback);
},
getfacilityData(fuc) {

this.subsy.token = fuc.token
this.subsy.interUserCode = fuc.interUserCode
this.subsy.subsyCode = fuc.subsyCode
}
},
mounted() {


var self = this;
self.starttime = self.myTool.commonTool.getmonthFirst();
self.endtime = self.myTool.commonTool.getmonthEnd();

this.getsubsyCode();
},
activated() {


},
deactivated() {

},
beforeDestroy() {}
}




