export default {

  name: 'nurseRecords',
  data() {
    return {
      pageTotal: "0",
      listData: false,
      input: "",
      subvalue: "",
      parm: {
        subsyCode: "",
        pagingNum: "0",
        exterUserName: "",
        roomNo: "",
        rommStatus: "1",
      },
      options: [{
        value: '1',
        label: '已入住'
      }, {
        value: '2',
        label: '已离所'
      }, {
        value: '3',
        label: '本月离所'
      }],
      suboptions: [],
      users: [
        //   {
        //     value0:"1",
        //     value1:"8201",
        //     value2:{mom:"王丽华",baby:"王丽华之女"},
        //     value3:"上海静安会所",
        //     value4:"已离所"
        // },{
        //     value0:"2",
        //     value1:"8201",
        //     value2:{mom:"王丽华",baby:"王丽华之女"},
        //     value3:"上海静安会所",
        //     value4:"已离所"
        // },{
        //     value0:"3",
        //     value1:"8201",
        //     value2:{mom:"王丽华",baby:"王丽华之女"},
        //     value3:"上海静安会所",
        //     value4:"已离所"
        // },{
        //     value0:"3",
        //     value1:"8201",
        //     value2:{mom:"王丽华",baby:"王丽华之女"},
        //     value3:"上海静安会所",
        //     value4:"已离所"
        // },{
        //     value0:"4",
        //     value1:"8201",
        //     value2:{mom:"王丽华",baby:"王丽华之女"},
        //     value3:"上海静安会所",
        //     value4:"已离所"
        // },
      ]
    }
  },
  // computed: {
  //   classObject: function () {
  //     return {
  //       'bg_color2':intem.chice-1==dex
  //     }
  //   }
  // },
  filters: {
    myDate(val) {
      return val.slice(0, 4) + "年" + val.slice(4, 6) + "月" + val.slice(6, 8) + "日";　　　　
    }　
  },
  methods: {
    handleCurrentChange(val) {
      this.parm.pagingNum = val - 1;
      this.getnurseData(this.parm)
    },
    roomStatus(val) {
      // console.log("选择为", val)
    },
    opentip(index) {
      // console.log(index)
      this.boxshow = index + 1
    },
    linkData(type, mmcode, bbcode) {
      // this.router
      // this.$router.push('/nurseMod')
      this.$router.push({
        name: 'nurseMod',
        params: {
          usertype: type,
          exterUserCode: mmcode,
          babyCode: bbcode,
          subsyCode: this.subvalue
        }
      })
    },
    retSearch() {
      // console.log("parm-------", this.parm)
      this.getnurseData(this.parm)
    },
    getsubsyCode() {
      let self = this;
      let params = {
        "url": this.resource.SERVERTEST + "/babyskymng/nurs/getSubsyList",
        "method": "get"
      };
      // alert("token---->"+self.subsy.token)
      // console.log(params)
      let successcallback = function (res) {
        // console.log("getsubsyCode============", res)
        self.suboptions = []
        // console.log("res.length=========",res.data.length)
        if (res.getSubsyList.length != 0) {

          for (var i = 0; i < res.getSubsyList.length; i++) {
            self.suboptions.push({
              value: res.getSubsyList[i].subsyCode,
              label: res.getSubsyList[i].subsyName
            })
          }
          // console.log("self.subvalue============", self.suboptions)
          self.subvalue = self.suboptions[0].value;
          self.getnurseData(self.parm)
        } else {

        }
      };
      let failurecallback = function (res) {
        self.suboptions = []
        // self.$vux.toast.text('暂无数据', 'bottom')
        // console.log("error------------------->" + JSON.stringify(res))
      };
      self.resource.resourceUnit(params, successcallback, failurecallback);
    },
    getnurseData(parm) {
      let self = this;
      let params = {
        // "url": this.resource.SERVERTEST + "/income/getCntrctIncomeStatis",
        // "url":  "http://192.168.1.198:9081/babyskyrsb/getComplBillReport",
        // "url":  this.WThost + "/babyskyrsb/roomBook/getCustRooserInfoWithBabyListForWeb",this.WThost
        // "url": this.HOST + "/babyskymng/nurs/getCustRooserInfoWithBabyListForWeb",WThost
        "url": this.resource.SERVERTEST + "/babyskymng/nurs/getCustRooserInfoWithBabyListForWeb",
        // "url": this.WThost + "/babyskyrsb/roomBook/getCustRooserInfoWithBabyListForWeb",
        "method": "get",
        // "headers": {
        //   "token": self.subsy.token
        // },
        "pa": {
          "subsyCode": self.subvalue,
          "exterUserName": parm.exterUserName,
          "roomNo": parm.roomNo,
          // "queryDate":"",
          "statusFlg": parm.rommStatus,
          // "statusFlg":"1",
          "pagingNum": parm.pagingNum
        }
      };
      // alert("token---->"+self.subsy.token)
      // console.log(params)
      let successcallback = function (res) {
        // console.log("res", res)
        // console.log("res.length=========",res.data.length)
        if (res.totalCount != 0) {
          self.listData = true;
          self.users = res.getRooserCustWithBabyDtlOutputBeanList
          self.pageTotal = res.totalCount
          // for(var i ;i <= res.data.ncareDataDtlBatchList.length; i++){
          //   if(res.data.ncareDataDtlBatchList[i].ncareTranCode = "NBIZ1003")
          // }
        } else {
          self.users = [];
          self.pageTotal = '0';
          self.listData = false;
          // self.$vux.toast.text('暂无数据', 'bottom')
        }
      };
      let failurecallback = function (res) {
        // self.$vux.toast.text('暂无数据', 'bottom')
        // console.log("error------------------->" + JSON.stringify(res))
      };
      self.resource.resourceUnit(params, successcallback, failurecallback);
    },
    retRecord() {
      this.$router.push("/")
    },
    getLocalTime(nS) {
      return new Date(parseInt(nS)).toLocaleString().replace(/:\d{1,2}$/, ' ');
    },
  },
  mounted() {
    // console.log(123123)
    this.getsubsyCode()
    // this.getnurseData(this.parm)
    this.resource.SERVERTEST = window.location.href.split("/babyskymng")[0]
    // console.log(window.location.href)
    // this.getnurseData(this.parm)

  }
}




