

import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
data() {
return {

sellertitle: "",
showScrollBox1: false,
showScrollBox2: false,
suboptions: [],
subvalue: "",
unfinished: [],
postpone: [],
starttime: "",
value2: "结束时间",
demo2: "无效客户",
list2: ["无效客户", "未处理客户"],
endtime: "",
index: 0,
btn: 0,
startDate: "",
enddate: "",
users: [],
subsy: {
token: "",
subsyCode: "1" ,
queryDate: ""
},
title: ["销售员", "咨询客户", "参观客户", "签约客户", "参观率", "签约率"]
};
},
filters: {
myCurrency(val) {
return new Date(parseInt(val) * 1000)
.toLocaleString()
.replace(/:\d{1,2}$/, " ");
}
},
created() {
window.SubmitH5Date = this.getfacilityData;

},
methods: {
change(value) {
console.log("change任务先触发");
console.log(this.starttime);
console.log(this.endtime);
var start =
this.starttime.split("-")[0] +
this.starttime.split("-")[1] +
this.starttime.split("-")[2];
var end =
this.endtime.split("-")[0] +
this.endtime.split("-")[1] +
this.endtime.split("-")[2];
console.log(start, "传给任务的开始");
console.log(end, "传给任务的结束");
this.getTaskStatis(start, end);
},

linkDeferment(intem, index) {
if (intem.delayQty > 0) {
this.postpone = intem.delayList;
this.showScrollBox1 = true;
}
},
unfinish(intem, index) {
if (intem.incompleteQty > 0) {
this.unfinished = intem.incompleteList;
this.showScrollBox2 = true;
}
},


getsubsyCode() {
console.log(this.subsy.subsyCode,"一开始会所代码")
console.log("获取会所名称列表任务开始")
let self = this;
let params = {
url: this.resource.SERVERTEST + "/babyskymng/nurs/getSubsyList",
method: "get"
};


let successcallback = function(res) {
console.log("getsubsyCode============", res)
self.suboptions = [];

if (res.getSubsyList.length != 0) {
for (var i = 0; i < res.getSubsyList.length; i++) {
self.suboptions.push({
value: res.getSubsyList[i].subsyCode,
label: res.getSubsyList[i].subsyName
});
}

self.subsy.subsyCode = self.suboptions[0].value;
self.change()

var timer1 = window.setTimeout(function() {
self.starttime = self.myTool.commonTool.getmonthFirst();
self.endtime = self.myTool.commonTool.getmonthEnd();


}, 100);
} else {
}
};
let failurecallback = function(res) {
console.log("获取会所列表内容失败")
self.suboptions = [];
self.$vux.toast.text('暂无数据', 'bottom')

};
self.resource.resourceUnit(params, successcallback, failurecallback);
},

getTaskStatis(start, end) {
console.log("hahah")
this.$vux.loading.show({
text: '加载中'
})
let self = this;
let params = {
url: this.resource.SERVERTEST + "/babyskymng/task/getTaskPractitioners",
method: "post",
body: {
subsyCode: self.subsy.subsyCode,
queryStartDate: start,
queryEndDate: end,
taskTranCode: "00340003",
rollCode: "ROLL012"
}
};
console.log(params,"获取数据的参数")
let successcallback = function(res) {
self.$vux.loading.hide()
self.$vux.toast.text('暂无数据', 'bottom')
if (res.data.userTaskStatisList != null) {
self.users = res.data.userTaskStatisList;

} else {
self.users = res.data.userTaskStatisList;
console.log("请求成功");
self.$vux.toast.text("暂无数据", "bottom");
}
};
let failurecallback = function(res) {
self.$vux.loading.hide()
self.$vux.toast.text('暂无数据', 'bottom')
console.log("请求失败 = " + res);
self.$vux.toast.text("暂无数据", "bottom");

};
self.resource.resourceUnit(params, successcallback, failurecallback);
},

getfacilityData(fuc) {
this.subsy.token = fuc.token;
this.subsy.interUserCode = fuc.interUserCode;
this.subsy.subsyCode = fuc.subsyCode;
}
},

mounted() {



var self = this;
self.starttime = self.myTool.commonTool.getmonthFirst();
self.endtime = self.myTool.commonTool.getmonthEnd();

this.getsubsyCode();


},
activated() {


},
deactivated() {

},
beforeDestroy() {}
};



