

import XDialog from 'vux/src/components/x-dialog/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Loading from 'vux/src/components/loading/index.vue'


export default {



data() {
return {

unfinished: [],
postpone: [],
showScrollBox2:false,
showScrollBox1:false,
all: '',
starttime: '选择开始时间',
value2: '结束时间',
startDate: "",
enddate: "",
demo2: '无效客户',
list1: [],
list4: [],
list5: [],
suboptions: [],
subvalue: "",
list6: {
text: '',
subtext: ''
},
subsy: {
token: '',
subsyCode: '',
queryDate: ''
},
endtime: '选择结束时间',
index: 0,
btn: 0,
"total":"1070350.00"
}
},
filters: {
myCurrency(val) {
return new Date(parseInt(val) * 1000).toLocaleString().replace(/:\d{1,2}$/, ' ');
}
},
created() {
window.SubmitH5Date = this.getfacilityData;

},
methods: {
linkDeferment(intem, index) {
if (intem.delayFinishedCount > 0) {
this.postpone = intem.delayFinishedOutputBeanList
this.showScrollBox1 = true
}
},
unfinish(intem, index) {
if (intem.noFinishCount > 0) {
this.unfinished = intem.noFinishOutputBeanList
this.showScrollBox2 = true
}
},
change(value) {
var start = this.starttime.split('-')[0] + this.starttime.split('-')[1] + this.starttime.split('-')[2]
var end = this.endtime.split('-')[0] + this.endtime.split('-')[1] + this.endtime.split('-')[2]
console.log(this.starttime)
console.log(this.endtime)
this.getCntrctIncomeStatis(start, end)
},
getsubsyCode() {
let self = this;
let params = {

"url": this.resource.SERVERTEST + "/babyskymng/nurs/getSubsyList",

"method": "get"

};

let successcallback = function (res) {

self.suboptions = []

if (res.getSubsyList.length != 0) {

for (var i = 0; i < res.getSubsyList.length; i++) {
self.suboptions.push({
value: res.getSubsyList[i].subsyCode,
label: res.getSubsyList[i].subsyName
})
}

self.subsy.subsyCode = self.suboptions[0].value;
self.change()



var timer1 = window.setTimeout(function () {
self.endtime = self.myTool.commonTool.getmonthFirst()
self.starttime = self.myTool.commonTool.getmonthEnd()
}, 100);
} else {

}
};
let failurecallback = function (res) {
self.suboptions = []


};
self.resource.resourceUnit(params, successcallback, failurecallback);
},
getCntrctIncomeStatis(start, end) {
this.$vux.loading.show({
text: '加载中'
})
let self = this;
let params = {
"url": this.resource.SERVERTEST + "/babyskymng/report/getComplBillReportForWeb",


"method": "post",



"body": {

startDate:start,
endDate:end,

subsyCode: self.subsy.subsyCode,


}
};


let successcallback = function (res) {
self.$vux.loading.hide()
if (res.data != null) {

self.list1 = res.data.complReportListOutputBeanList
self.list4 = res.data;
self.list5 = [];











} else {
self.$vux.toast.text('暂无数据', 'bottom')
}
};
let failurecallback = function (res) {
self.$vux.loading.hide()
self.$vux.toast.text('暂无数据', 'bottom')

};
self.resource.resourceUnit(params, successcallback, failurecallback);
},
getfacilityData(fuc) {

this.subsy.token = fuc.token
this.subsy.subsyCode = fuc.subsyCode
},
















},
mounted() {


var self = this;
self.starttime = self.myTool.commonTool.getmonthFirst();
self.endtime = self.myTool.commonTool.getmonthEnd();

this.getsubsyCode();
},
activated() {


},
deactivated() {

},
beforeDestroy() {}
}




