export default {

  name: 'nurseRecords',
  data() {
    return {
      value2:new Date(),
      num1: 1,
      num2:1,
      roleCode:"NBIZ1005",
      timelist:[{time:new Date()}],
      dialogTitle:"添加参数",
      formLabelWidth: '120px',
      maxVal:"",
      minVal:"",
      isCollect:"1",
      form:{
        check:false,
        unitName:"",
        subsyCode:"",
        ncareTranCode:"",
        itemName:"",
        maxVal:"",
        minVal:"",
        newvalue:"1",
        valiData:[],
        ruleItemCountAndTime:{
          count:"",
          day:"",
          time:[]
        }
      },
      valiDatabox:{
          input:[{value:"",status:false}],
          range:{
            max:"",
            min:""
          }
        },
      dialogVisible: false,
      loading:false,
      DataList:[],
      input:"",
      value:"上海静安会所",
      value1:"",
      activeName2: 'nursmm',
      collectOption:[{
         value:"1",
        label:"否"
      },{
         value:"2",
        label:"是"
      }],
      checkoption:[{
        value:"1",
        label:"下拉项"
      },{
        value:"2",
        label:"数值范围"
      }],
      subvalue:'3130318001',
      suboptions:[],
      parm:{
        
      },
    //   options: [{
    //   value: '1',
    //   label: '上海静安店'
    // }, {
    //   value: '2',
    //   label: '上海静安店'
    // }, {
    //   value: '3',
    //   label: '上海静安店'
    // }],
      pickerOptions1: {
      disabledDate(time) {
        return time.getTime() > Date.now();
      }
      },
      nurselist:{list:[],msg:""},
      doclist:{list:[],msg:""},
      babycare:{list:[],msg:""},
      DataList:[],
      newdataList:[],
      testvalue:[],
    }
  },
  // computed: {
  //   classObject: function () {
  //     return {
  //       'bg_color2':intem.chice-1==dex
  //     }
  //   }
  // },
  watch: {
        num2(newval,oldval) {
          if(newval == 1){
           this.timelist = [{time:new Date()}]
         }else{
           if(newval > oldval){
              this.timelist.push({time:new Date()})
         }else{
           this.timelist.pop()
         }
         }
        }
    },
  filters: {
    filterTime(val){
      return val.slice(0, 2) + ":" + val.slice(2, 4) + ":" + val.slice(4, 6) + "";
    },
    fifterDay(val){
      if(val == "1"){
        return "";
      }else{
        return val
      }
      
    }
  },
  methods: {
    delNcareBaseData(TranCode,ItemCode) {
      let self = this;
    let params = {
      "url": this.resource.SERVERTEST + "/babyskymng/ncare/delNcareBaseData",
      "method": "post",
      "body": {
            ncareItemCode:ItemCode,
            subsyCode:'3130318001',
            ncareTranCode:TranCode
        }
    };
    let successcallback = function (res) {
      // console.log("res", res)
      // self.dialogVisible = false
      

      if (res.status == 200) {
        self.$notify.success({
          title: '成功',
          message: '删除参数成功',
          showClose: false
        });
        self.getNcareBaseData()
      } else {
        self.$notify.error({
          title: '错误',
          message: res.msg,
          showClose: false
        });
        // self.$vux.toast.text('暂无数据', 'bottom')
      }
    
    };
    let failurecallback = function (res) {
      // self.dialogVisible = false
      self.$notify.error({
          title: '错误',
          message: res.msg,
          showClose: false
        });
      // self.$vux.toast.text('暂无数据', 'bottom')
      // console.log("error------------------->" + JSON.stringify(res))
    };
    self.resource.resourceUnit(params, successcallback, failurecallback);

    },
    addvaliData() {
      this.valiDatabox.input.push({value:"",status:false})
    },
    remvaliData() {
      this.valiDatabox.input.pop()
    },
    handleChange(){
      // console.log("form.newvalue",this.form.newvalue)
    },
    retAdd(value){
      this.valiDatabox = {
          input:[{value:"",status:false}],
          range:{
            max:"",
            min:""
          }
        }
      this.form = {
        unitName:"",
        check:false,
        subsyCode:"",
        ncareTranCode:"",
        itemName:"",
        maxVal:null,
        minVal:null,
        newvalue:"1",
        valiData:[],
        ruleItemCountAndTime:{
          count:"",
          day:"",
          time:[]
        }
      }
      this.isCollect = "1"
      this.num2 = 1
      this.num1 = 1
      this.maxVal = ""
      this.minVal = ""
      // this.timelist = []
      // this.timelist = [{time:new Date()}]
      this.dialogVisible = true;
      this.form.ncareTranCode = value
    },
    formSubmit(){
      this.form.subsyCode = this.subvalue
      if(this.form.newvalue == "1"){
        // console.log("this.valiDatabox.input=======",this.valiDatabox.input)
        // this.form.valiData.push({"valiOprCode":'00180001',"valiDataVal":'' })
        for(var i =0;i<this.valiDatabox.input.length;i++){
        this.form.valiData.push({"valiOprCode":'00180001',"valiDataVal":this.valiDatabox.input[i].value,status: this.valiDatabox.input[i].status})
        }
      }else{
        this.form.maxVal = this.maxVal
        this.form.minVal = this.minVal
        this.form.valiData.push({"valiOprCode":'00180002',"valiDataVal":this.valiDatabox.range.min })
        this.form.valiData.push({"valiOprCode":'00180003',"valiDataVal":this.valiDatabox.range.max })
      }
      for(var i =0;i<this.timelist.length;i++){
        this.form.ruleItemCountAndTime.time.push(this.myTool.commonTool.gettimedate(this.timelist[i].time))
        }
        this.form.ruleItemCountAndTime.count = this.num2
        this.form.ruleItemCountAndTime.day = this.num1
        if(this.isCollect == "1"){
          this.form.check = false
        }else{
          this.form.check = true
        }
      // console.log(this.timelist)
      // console.log(this.form)
        this.formSubmitData()
    },
    handleClick(tab){
      // this.getNcareBaseData()
      // console.log(tab.name)
      switch (tab.name) {
                case 'nursmm':
                    //系统
                    this.roleCode = "NBIZ1005"
                    this.getNcareBaseData()
                    return;
                case 'nursbb':
                    //团队
                    this.roleCode = "NBIZ2005"
                    this.getNcareBaseData()
                    return;
                default:
                    return;
            } 
    },
    opentip(index) {
      // console.log(index)
      this.boxshow = index + 1
    },
  formSubmitData() {
    let self = this;
    let params = {
      "url": this.resource.SERVERTEST + "/babyskymng/ncare/crtNcareBaseData",
      "method": "post",
      "body": self.form
    };
    let successcallback = function (res) {
      // console.log("res", res)
      self.dialogVisible = false
      

      if (res.status == 200) {
        self.$notify.success({
          title: '成功',
          message: '添加参数成功',
          showClose: false
        });
        self.getNcareBaseData()
      } else {
        self.$notify.error({
          title: '错误',
          message: res.msg,
          showClose: false
        });
        // self.$vux.toast.text('暂无数据', 'bottom')
      }
    
    };
    let failurecallback = function (res) {
      self.dialogVisible = false
      self.$notify.error({
          title: '错误',
          message: res.msg,
          showClose: false
        });
      // self.$vux.toast.text('暂无数据', 'bottom')
      // console.log("error------------------->" + JSON.stringify(res))
    };
    self.resource.resourceUnit(params, successcallback, failurecallback);
  },

   getNcareBaseData() {
    // console.log(123123)
    this.loading = true;
    this.DataList = [];
    this.newdataList = [];
    let self = this;
    let params = {
      
      // "url":  "http://192.168.1.198:9081/babyskyrsb/getComplBillReport",
      // "url":  this.HOST + "/babyskyrsb/roomBook/getCustRooserInfoWithBabyListForWeb",http://192.168.1.188:9083/
      // "url": this.HOST + "/babyskymng/nurs/getCustRooserInfoWithBabyListForWeb",
      "url": this.resource.SERVERTEST + "/babyskymng/ncare/getNcareBaseData",
      // "url": this.TFhost + "/babyskymng/ncare/getNcareBaseData",
      // "url": this.WThost + "/babyskyrsb/nurs/getSubsyList",
      "method": "get",
      // "headers": {
      //   "token": self.subsy.token
      // },
      "pa": {
        "subsyCode": self.subvalue,
        "ncareTranCode": self.roleCode
      }
    };
    // alert("token---->"+self.subsy.token)
    // console.log(params)
    let successcallback = function (res) {
      self.loading = false;
      // console.log("getNcareBaseData============", res)
      for ( var i = 0;i< res.data.length;i++ ){
        if(res.data[i].defFlag){
          self.DataList.push(res.data[i])
        }else{
          self.newdataList.push(res.data[i])
        }
      }

    };
    let failurecallback = function (res) {
      self.loading = false;
      // console.log("1111111=====",self.loading)
      // self.suboptions = []
      // self.$vux.toast.text('暂无数据', 'bottom')
      // console.log("error------------------->" + JSON.stringify(res))
    };
    self.resource.resourceUnit(params, successcallback, failurecallback);
  },
    retRecord() {
      this.$router.push("/babyskymng/nurs/list/nurseRecords")
    },
    getLocalTime(nS) {
    return new Date(parseInt(nS)).toLocaleString().replace(/:\d{1,2}$/, ' ');
  },
  },
  mounted() {
    this.getNcareBaseData()
    //  this.getNcareBaseData("NBIZ1001")
    // this.loading[0] = false;
    this.value1 = this.myTool.commonTool.getcurrdate()
    //  console.log("time=========",this.getLocalTime(1515340580000))
  }
}




