

var echarts = require('echarts/lib/echarts')
require('echarts/lib/chart/pie')
require('echarts/lib/component/legend')
require('echarts/lib/component/title')

export default {
data() {
return {

ehartsstatus: true,
starttime: '选择开始时间',
value2: '结束时间',
startDate: "",
enddate: "",
demo2: '咨询客户',
list2: ['咨询客户', '参观客户', '签约客户'],
endtime: '选择结束时间',
index: 0,
btn: 0,
suboptions: [],
subvalue: "",
subsy: {
token: '',
subsyCode: '',
queryDate: ''
},
datalist: {
data1: [],
data2: []
},
users: [],
}
},



created() {
window.SubmitH5Date = this.getfacilityData;


},
methods: {
change(value) {
var start = this.starttime.split('-')[0] + this.starttime.split('-')[1] + this.starttime.split('-')[2]
var end = this.endtime.split('-')[0] + this.endtime.split('-')[1] + this.endtime.split('-')[2]
this.getTaskStatis(start, end)
},
chartstart() {
let myChart = echarts.init(document.getElementById('chart'));
let option = {
backgroundColor: "#fff",
tooltip: {
trigger: 'item',
formatter: "{a} <br/>{b} : {c} ({d}%)"
},
legend: {
left: 'center',
bottom: "bottom",
orient: 'horizontal',
data: this.datalist.data2,
selectedMode: false,
},
series: [{
name: '4',
type: 'pie',
radius: '45%',
selectedMode: 'single',
center: ['50%', '40%'],
data: this.datalist.data1,
avoidLabelOverlap: true,
itemStyle: {
emphasis: {
shadowBlur: 10,
shadowOffsetX: 0,
shadowColor: 'rgba(0, 0, 0, 0.5)'
}
}
}]
};
myChart.setOption(option);
},
getsubsyCode() {
let self = this;
let params = {

"url": this.resource.SERVERTEST + "/babyskymng/nurs/getSubsyList",
"method": "get"
};


let successcallback = function (res) {

self.suboptions = []

if (res.getSubsyList.length != 0) {

for (var i = 0; i < res.getSubsyList.length; i++) {
self.suboptions.push({
value: res.getSubsyList[i].subsyCode,
label: res.getSubsyList[i].subsyName
})
}

self.subsy.subsyCode = self.suboptions[0].value;
self.change();



var timer1 = window.setTimeout(function () {
self.starttime = self.myTool.commonTool.getmonthFirst()
self.endtime = self.myTool.commonTool.getmonthEnd()

}, 100);
} else {

}
};
let failurecallback = function (res) {
self.suboptions = []


};
self.resource.resourceUnit(params, successcallback, failurecallback);
},
getTaskStatis(start, end) {
this.$vux.loading.show({
text: '加载中'
})
let self = this;
let params = {
"url": this.resource.SERVERTEST + "/babyskymng/sale/getOrderSourceReport",

"method": "post",
"body": {




subsyCode: self.subsy.subsyCode,
queryStartDate: start,
queryEndDate: end,
}
};
let successcallback = function (res) {
self.$vux.loading.hide()
self.datalist.data1 = [];
self.datalist.data2 = []
if (res.data.sourceDtlList != null && res.data.sourceList != null) {
self.ehartsstatus = true
let labelbox = {
normal: {
position: 'outside',
formatter: function (e) {
if (e.percent == 0) {
return   '0.00%'
} else {

return e.percent + '%'
}
},
textStyle: {
color: '',
fontSize: 12
}
}
}
self.datalist.data1 = [];
self.datalist.data2 = [];
for (var i = 0; i < res.data.sourceList.length; i++) {
self.datalist.data1.push({
value: res.data.sourceList[i].totalPrice,
name: res.data.sourceList[i].sourceName,
label: labelbox
})
self.datalist.data2.push(res.data.sourceList[i].sourceName, )
}
self.users = res.data.sourceDtlList
self.chartstart()
} else {
self.ehartsstatus = false
self.$vux.toast.text('暂无数据', 'bottom')
}
};
let failurecallback = function (res) {
self.$vux.loading.hide()
self.ehartsstatus = false
self.$vux.toast.text('暂无数据', 'bottom')
};
self.resource.resourceUnit(params, successcallback, failurecallback);
},
getfacilityData(fuc) {
this.subsy.token = fuc.token
this.subsy.subsyCode = fuc.subsyCode

}
},
mounted() {


var self = this;
self.starttime = self.myTool.commonTool.getmonthFirst();
self.endtime = self.myTool.commonTool.getmonthEnd();

this.getsubsyCode();
},
activated() {

},
deactivated() {

},
beforeDestroy() {}
}




